import React, { useState, useEffect, useCallback } from 'react';
import { useNavigate, useParams } from 'react-router-dom';
import { useAuth } from '../contexts/AuthContext';
import { projectsAPI, sessionsAPI } from '../services/api-v3';
import Button from '../components/Button';
import Card from '../components/Card';
import Loading from '../components/Loading';
import './SessionsPage.css';

function SessionsPage() {
  const { user } = useAuth();
  const [project, setProject] = useState(null);
  const [sessions, setSessions] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [showCreateModal, setShowCreateModal] = useState(false);
  const [newSession, setNewSession] = useState({ name: '' });
  const { projectId } = useParams();
  const navigate = useNavigate();

  const loadProjectAndSessions = useCallback(async () => {
    try {
      setLoading(true);
      
      // Load project details
      const projectResponse = await projectsAPI.getById(projectId);
      if (projectResponse.success) {
        setProject(projectResponse.data);
      }

      // Load sessions
      const sessionsResponse = await sessionsAPI.getByProject(projectId);
      if (sessionsResponse.success) {
        setSessions(sessionsResponse.data);
      }
    } catch (err) {
      setError(err.message);
    } finally {
      setLoading(false);
    }
  }, [projectId]);

  useEffect(() => {
    loadProjectAndSessions();
  }, [loadProjectAndSessions]);

  const handleCreateSession = async () => {
    if (!newSession.name.trim()) {
      alert(' ,   ');
      return;
    }

    try {
      const response = await sessionsAPI.create({
        projectId: projectId,  // Backend  camelCase
        userId: user.id,       // Backend  userId
        name: newSession.name
      });

      if (response.success) {
        // Backend  { data: { session, parameters, weights } }
        const createdSession = response.data.session || response.data;
        setSessions([createdSession, ...sessions]);
        setShowCreateModal(false);
        setNewSession({ name: '' });
        
        // Navigate to generation page
        navigate(`/projects/${projectId}/sessions/${createdSession.id}/generate`);
      }
    } catch (err) {
      alert('  : ' + err.message);
    }
  };

  const handleDeleteSession = async (sessionId) => {
    if (!window.confirm(' ?      .')) {
      return;
    }

    try {
      await sessionsAPI.delete(sessionId);
      setSessions(sessions.filter(s => s.id !== sessionId));
    } catch (err) {
      alert(' : ' + err.message);
    }
  };

  const handleRenameSession = async (sessionId, currentName) => {
    const newName = prompt('  :', currentName);
    if (!newName || newName === currentName) return;

    try {
      const response = await sessionsAPI.update(sessionId, { name: newName });
      if (response.success) {
        setSessions(sessions.map(s => 
          s.id === sessionId ? { ...s, name: newName } : s
        ));
      }
    } catch (err) {
      alert(' : ' + err.message);
    }
  };

  if (loading) {
    return (
      <div className="sessions-page">
        <Loading text=" ..." />
      </div>
    );
  }

  if (!project) {
    return (
      <div className="sessions-page">
        <div className="error-container">
          <h2>   </h2>
          <Button onClick={() => navigate('/projects')}>   </Button>
        </div>
      </div>
    );
  }

  return (
    <div className="sessions-page">
      <div className="sessions-container">
        {/* Project Header */}
        <div className="sessions-header">
          <div>
            <Button
              variant="secondary"
              size="small"
              onClick={() => navigate('/projects')}
              style={{ marginBottom: '1rem' }}
            >
                 
            </Button>
            <h1> {project.name}</h1>
            <div className="project-meta-header">
              <span className="category-badge">{project.category}</span>
              {project.description && <p className="project-desc">{project.description}</p>}
            </div>
          </div>
          <Button onClick={() => setShowCreateModal(true)} size="large">
              
          </Button>
        </div>

        {error && (
          <div className="error-banner">
             {error}
          </div>
        )}

        {sessions.length === 0 ? (
          <div className="empty-state">
            <div className="empty-icon"></div>
            <h2> </h2>
            <p>     AI </p>
            <Button onClick={() => setShowCreateModal(true)} size="large">
                 
            </Button>
          </div>
        ) : (
          <div className="sessions-list">
            {sessions.map((session, index) => (
              <Card key={session.id} className="session-card">
                <div className="session-card-header">
                  <div className="session-info">
                    <span className="session-number"> {index + 1}</span>
                    <h3 className="session-name">{session.name}</h3>
                  </div>
                  <div className="session-header-actions">
                    <button
                      className="session-action-btn"
                      onClick={() => handleRenameSession(session.id, session.name)}
                      title=""
                    >
                      
                    </button>
                    <button
                      className="session-action-btn delete"
                      onClick={() => handleDeleteSession(session.id)}
                      title=""
                    >
                      
                    </button>
                  </div>
                </div>

                <div className="session-stats">
                  <div className="stat-item">
                    <span className="stat-icon"></span>
                    <div>
                      <div className="stat-value">{session.generations_count || 0}</div>
                      <div className="stat-label"></div>
                    </div>
                  </div>
                  <div className="stat-item">
                    <span className="stat-icon"></span>
                    <div>
                      <div className="stat-value">{session.ratings_count || 0}</div>
                      <div className="stat-label"></div>
                    </div>
                  </div>
                  <div className="stat-item">
                    <span className="stat-icon"></span>
                    <div>
                      <div className="stat-value">{session.parameters_count || 0}</div>
                      <div className="stat-label"></div>
                    </div>
                  </div>
                </div>

                <div className="session-meta">
                  <span>: {new Date(session.created_at).toLocaleDateString('uk-UA')}</span>
                  {session.updated_at && session.updated_at !== session.created_at && (
                    <span>: {new Date(session.updated_at).toLocaleDateString('uk-UA')}</span>
                  )}
                </div>

                <div className="session-actions">
                  <Button
                    onClick={() => navigate(`/projects/${projectId}/sessions/${session.id}/gallery`)}
                    variant="secondary"
                    size="small"
                  >
                     
                  </Button>
                  {session.ratings_count > 0 && (
                    <Button
                      onClick={() => navigate(`/projects/${projectId}/sessions/${session.id}/weights`)}
                      variant="secondary"
                      size="small"
                    >
                       
                    </Button>
                  )}
                  <Button
                    onClick={() => navigate(`/projects/${projectId}/sessions/${session.id}/generate`)}
                    variant="primary"
                    size="small"
                  >
                    {session.generations_count > 0 ? ' ' : ' '} 
                  </Button>
                </div>
              </Card>
            ))}
          </div>
        )}
      </div>

      {/* Create Session Modal */}
      {showCreateModal && (
        <div className="modal-overlay" onClick={() => setShowCreateModal(false)}>
          <div className="modal-content" onClick={(e) => e.stopPropagation()}>
            <div className="modal-header">
              <h2>   </h2>
              <button className="modal-close" onClick={() => setShowCreateModal(false)}>
                
              </button>
            </div>

            <div className="modal-body">
              <div className="form-group">
                <label className="form-label"> </label>
                <input
                  type="text"
                  className="form-input"
                  placeholder={`:  ${sessions.length + 1} - ${project.category}`}
                  value={newSession.name}
                  onChange={(e) => setNewSession({ name: e.target.value })}
                  autoFocus
                />
              </div>

              <div className="info-box">
                <strong>  :</strong>
                <ul>
                  <li>       </li>
                  <li>    </li>
                  <li>     </li>
                  <li>     </li>
                </ul>
              </div>
            </div>

            <div className="modal-footer">
              <Button variant="secondary" onClick={() => setShowCreateModal(false)}>
                
              </Button>
              <Button onClick={handleCreateSession}>
                   
              </Button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}

export default SessionsPage;
