import React, { useState, useEffect, useCallback } from 'react';
import { useNavigate, useParams } from 'react-router-dom';
import { useAuth } from '../contexts/AuthContext';
import { projectsAPI, sessionsAPI, generationAPI } from '../services/api-v3';
import Button from '../components/Button';
import Card from '../components/Card';
import Loading from '../components/Loading';
import './GeneratePageV3.css';

//    
const AVAILABLE_MODELS = [
  {
    key: 'seedream-4',
    name: ' Seedream 4',
    description: ' , 2K ',
    speed: '~1 ',
    price: '$0.03',
    recommended: true
  },
  {
    key: 'nano-banana-pro',
    name: ' Nano Banana Pro',
    description: 'Gemini SOTA, ',
    speed: '~45 ',
    price: '$0.025',
    recommended: false
  },
  {
    key: 'flux-schnell',
    name: ' FLUX Schnell',
    description: ' ',
    speed: '~30 ',
    price: '$0.003',
    recommended: false
  },
  {
    key: 'flux-dev',
    name: ' FLUX Dev',
    description: ' ',
    speed: '~2 ',
    price: '$0.025',
    recommended: false
  },
  {
    key: 'sdxl',
    name: ' Stable Diffusion XL',
    description: ' ',
    speed: '~1 ',
    price: '$0.008',
    recommended: false
  }
];

function GeneratePageV3() {
  const { user } = useAuth();
  const [project, setProject] = useState(null);
  const [session, setSession] = useState(null);
  const [prompt, setPrompt] = useState('');
  const [selectedModel, setSelectedModel] = useState('seedream-4');
  const [count, setCount] = useState(10);
  const [generating, setGenerating] = useState(false);
  const [generatedItems, setGeneratedItems] = useState([]);
  const [currentIndex, setCurrentIndex] = useState(0);
  const [progress, setProgress] = useState({ current: 0, total: 0 });
  const [error, setError] = useState(null);
  const [showCommentModal, setShowCommentModal] = useState(false);
  const [comment, setComment] = useState('');
  const [pendingRating, setPendingRating] = useState(null);
  const [showCompletionScreen, setShowCompletionScreen] = useState(false);
  const [loadingNext, setLoadingNext] = useState(false);
  const [generationComplete, setGenerationComplete] = useState(false);
  
  // Swipe state
  const [touchStart, setTouchStart] = useState({ x: 0, y: 0 });
  const [touchEnd, setTouchEnd] = useState({ x: 0, y: 0 });
  const [isDragging, setIsDragging] = useState(false);
  const [dragOffset, setDragOffset] = useState({ x: 0, y: 0 });
  
  const { projectId, sessionId } = useParams();
  const navigate = useNavigate();

  const loadProjectAndSession = useCallback(async () => {
    try {
      const projectResponse = await projectsAPI.getById(projectId);
      if (projectResponse.success) {
        setProject(projectResponse.data);
      }

      const sessionResponse = await sessionsAPI.getById(sessionId);
      if (sessionResponse.success) {
        setSession(sessionResponse.data);
      }
    } catch (err) {
      setError(' : ' + err.message);
    }
  }, [projectId, sessionId]);

  useEffect(() => {
    loadProjectAndSession();
  }, [loadProjectAndSession]);

  const handleGenerate = async () => {
    if (!prompt.trim()) {
      alert(' ,  prompt');
      return;
    }

    setGenerating(true);
    setGeneratedItems([]);
    setCurrentIndex(0);
    setProgress({ current: 0, total: count });
    setError(null);
    setLoadingNext(false);
    setGenerationComplete(false);

    try {
      //  STREAMING GENERATION:   
      for (let i = 0; i < count; i++) {
        console.log(` Generating ${i + 1}/${count}...`);
        
        try {
          const response = await generationAPI.generate({
            sessionId: sessionId,
            projectId: projectId,
            userId: user.id,
            userPrompt: prompt,
            count: 1,              //   !
            model: selectedModel
          });

          if (response.success && response.results && response.results[0]?.success) {
            const newItem = response.results[0].content;
            
            //    
            setGeneratedItems(prev => [...prev, newItem]);
            setProgress({ current: i + 1, total: count });
            
            //     -   loader
            if (i === 0) {
              setGenerating(false);
              setLoadingNext(true); //    
            }
            
            console.log(` Generated ${i + 1}/${count}`);
          } else {
            console.error(` Failed generation ${i + 1}`);
          }
        } catch (err) {
          console.error(`Error generating ${i + 1}:`, err);
        }
      }
      
      //  
      setGenerationComplete(true);
      setLoadingNext(false);
      
    } catch (err) {
      setError(err.message || '  ');
      setGenerating(false);
      setLoadingNext(false);
    }
  };

  const handleSwipe = async (direction) => {
    const currentItem = generatedItems[currentIndex];
    
    //  UX FIX:      
    const isLastItem = currentIndex === generatedItems.length - 1;
    
    //         - !
    if (isLastItem && (loadingNext || generating)) {
      console.log(' Next photo is not ready yet, please wait...');
      //   warning  
      return;
    }
    
    if (direction === 'down') {
      // Skip - no rating
      moveToNext();
    } else {
      // Show comment modal for rating
      setPendingRating({
        content_id: currentItem.id,
        direction: direction
      });
      setShowCommentModal(true);
    }
  };

  const submitRating = async () => {
    if (!pendingRating) return;

    //  rating
    const ratingMap = {
      'up': 3,
      'right': 1,
      'left': -1,
      'super-down': -3,
      'down': 0
    };

    const ratingData = {
      contentId: pendingRating.content_id,
      rating: ratingMap[pendingRating.direction],
      comment: comment || null
    };

    //   modal     (  API)
    setShowCommentModal(false);
    setComment('');
    setPendingRating(null);
    moveToNext();

    //  rating  
    try {
      await generationAPI.rate(ratingData);
      console.log(' Rating saved in background');
    } catch (err) {
      console.error(' Failed to save rating:', err);
      //   alert    flow
    }
  };

  const skipRating = () => {
    setShowCommentModal(false);
    setComment('');
    setPendingRating(null);
    moveToNext();
  };

  const moveToNext = () => {
    //     - 
    if (currentIndex < generatedItems.length - 1) {
      setCurrentIndex(currentIndex + 1);
    } 
    //     -    (loader )
    else if (loadingNext || generating) {
      //    , UI  loader
      console.log(' Waiting for next generation...');
    }
    //      -  completion
    else if (generationComplete) {
      setShowCompletionScreen(true);
    }
  };

  const handleGenerateMore = () => {
    setShowCompletionScreen(false);
    setGeneratedItems([]);
    setCurrentIndex(0);
    setPrompt('');
    setLoadingNext(false);
    setGenerationComplete(false);
  };

  const handleViewGallery = () => {
    navigate(`/projects/${projectId}/sessions/${sessionId}/gallery`);
  };

  // Touch handlers for swipe
  const handleTouchStart = (e) => {
    setTouchStart({ x: e.touches[0].clientX, y: e.touches[0].clientY });
    setTouchEnd({ x: e.touches[0].clientX, y: e.touches[0].clientY });
    setIsDragging(true);
  };

  const handleTouchMove = (e) => {
    if (!isDragging) return;
    
    const currentX = e.touches[0].clientX;
    const currentY = e.touches[0].clientY;
    
    setTouchEnd({ x: currentX, y: currentY });
    setDragOffset({
      x: currentX - touchStart.x,
      y: currentY - touchStart.y
    });
  };

  const handleTouchEnd = () => {
    if (!isDragging) return;
    setIsDragging(false);
    
    const deltaX = touchEnd.x - touchStart.x;
    const deltaY = touchEnd.y - touchStart.y;
    const minSwipeDistance = 50;
    
    // Determine swipe direction
    if (Math.abs(deltaY) > Math.abs(deltaX)) {
      // Vertical swipe
      if (Math.abs(deltaY) > minSwipeDistance) {
        if (deltaY > 0) {
          // Swipe down - Skip
          handleSwipe('down');
        } else {
          // Swipe up - Superlike (+3)
          handleSwipe('up');
        }
      }
    } else {
      // Horizontal swipe
      if (Math.abs(deltaX) > minSwipeDistance) {
        if (deltaX > 0) {
          // Swipe right - Like (+1)
          handleSwipe('right');
        } else {
          // Swipe left - Dislike (-1)
          handleSwipe('left');
        }
      }
    }
    
    // Reset drag offset
    setDragOffset({ x: 0, y: 0 });
  };

  // Mouse handlers (for desktop)
  const handleMouseDown = (e) => {
    setTouchStart({ x: e.clientX, y: e.clientY });
    setTouchEnd({ x: e.clientX, y: e.clientY });
    setIsDragging(true);
  };

  const handleMouseMove = (e) => {
    if (!isDragging) return;
    
    setTouchEnd({ x: e.clientX, y: e.clientY });
    setDragOffset({
      x: e.clientX - touchStart.x,
      y: e.clientY - touchStart.y
    });
  };

  const handleMouseUp = () => {
    if (!isDragging) return;
    setIsDragging(false);
    
    const deltaX = touchEnd.x - touchStart.x;
    const deltaY = touchEnd.y - touchStart.y;
    const minSwipeDistance = 80;
    
    // Determine swipe direction
    if (Math.abs(deltaY) > Math.abs(deltaX)) {
      if (Math.abs(deltaY) > minSwipeDistance) {
        if (deltaY > 0) {
          handleSwipe('down');
        } else {
          handleSwipe('up');
        }
      }
    } else {
      if (Math.abs(deltaX) > minSwipeDistance) {
        if (deltaX > 0) {
          handleSwipe('right');
        } else {
          handleSwipe('left');
        }
      }
    }
    
    setDragOffset({ x: 0, y: 0 });
  };

  const currentItem = generatedItems[currentIndex];

  if (!project || !session) {
    return (
      <div className="generate-page-v3">
        <Loading text="..." />
      </div>
    );
  }

  return (
    <div className="generate-page-v3">
      <div className="generate-container-v3">
        {/* Header */}
        <div className="generate-header-v3">
          <div>
            <Button
              variant="secondary"
              size="small"
              onClick={() => navigate(`/projects/${projectId}/sessions`)}
              style={{ marginBottom: '1rem' }}
            >
                 
            </Button>
            <h1> {session.name}</h1>
            <div className="session-meta-header">
              <span className="project-badge">{project.name}</span>
              <span className="category-badge">{project.category}</span>
            </div>
          </div>
        </div>

        {error && (
          <div className="error-banner">
             {error}
          </div>
        )}

        {/* Prompt Input Section */}
        {!generating && generatedItems.length === 0 && (
          <Card className="prompt-card-v3">
            <h2>  AI </h2>
            <p className="prompt-hint">
              {session.generations_count === 0
                ? '      .        prompt.'
                : '        .'}
            </p>

            {/* Model Selection */}
            <div className="form-section">
              <label className="form-label">   AI:</label>
              <div className="models-grid">
                {AVAILABLE_MODELS.map(model => (
                  <div
                    key={model.key}
                    className={`model-card ${selectedModel === model.key ? 'selected' : ''} ${model.recommended ? 'recommended' : ''}`}
                    onClick={() => setSelectedModel(model.key)}
                  >
                    {model.recommended && <span className="recommended-badge"></span>}
                    <div className="model-name">{model.name}</div>
                    <div className="model-description">{model.description}</div>
                    <div className="model-stats">
                      <span className="model-speed"> {model.speed}</span>
                      <span className="model-price"> {model.price}</span>
                    </div>
                  </div>
                ))}
              </div>
            </div>

            {/* Count Selection */}
            <div className="form-section">
              <label className="form-label">  :</label>
              
              <div className="count-input-wrapper">
                <input
                  type="number"
                  min="1"
                  max="1000"
                  value={count}
                  onChange={(e) => {
                    const val = parseInt(e.target.value) || 1;
                    setCount(Math.min(1000, Math.max(1, val)));
                  }}
                  placeholder=" "
                  className="count-input-field"
                />
                <span className="count-label"></span>
              </div>

              <div className="count-quick-buttons">
                <span className="quick-label"> :</span>
                {[1, 5, 10, 20, 50, 100].map(num => (
                  <button
                    key={num}
                    type="button"
                    className={`count-quick-btn ${count === num ? 'active' : ''}`}
                    onClick={() => setCount(num)}
                  >
                    {num}
                  </button>
                ))}
              </div>

              <div className="count-info">
                <span> ~{Math.ceil(count * 1)} </span>
                <span> ~${(count * 0.03).toFixed(2)}</span>
                <span> {count} {count === 1 ? '' : count < 5 ? '' : ''}</span>
              </div>
            </div>

            <div className="form-section">
              <label className="form-label"> Prompt:</label>
              <textarea
                value={prompt}
                onChange={(e) => setPrompt(e.target.value)}
                placeholder={`    ...

  Dating: "Beautiful woman on the beach at sunset"
  Cars: "Red sports car on mountain road"
  Insurance: "Happy family with insurance protection"`}
                rows={5}
                className="prompt-textarea"
              />
            </div>

            <div className="generation-info">
              <div className="info-item">
                <span className="info-icon"></span>
                <div>
                  <strong>:</strong> {AVAILABLE_MODELS.find(m => m.key === selectedModel)?.name || 'Seedream 4'}
                </div>
              </div>
              <div className="info-item">
                <span className="info-icon"></span>
                <div>
                  <strong>:</strong> {count} {count === 1 ? '' : count < 5 ? '' : ''}
                </div>
              </div>
              <div className="info-item">
                <span className="info-icon"></span>
                <div>
                  <strong>:</strong> Step-by-step (   )
                </div>
              </div>
            </div>

            <Button
              onClick={handleGenerate}
              disabled={!prompt.trim()}
              size="large"
              style={{ width: '100%', marginTop: '1rem' }}
            >
                  
            </Button>
          </Card>
        )}

        {/* Generation Progress */}
        {generating && (
          <Card className="progress-card-v3">
            <Loading text={` ... ${progress.current}/${progress.total}`} />
            <div className="progress-bar">
              <div
                className="progress-fill"
                style={{ width: `${(progress.current / progress.total) * 100}%` }}
              />
            </div>
            <p className="progress-hint">
               '   .     .
            </p>
          </Card>
        )}

        {/* Completion Screen */}
        {showCompletionScreen && (
          <Card className="completion-card-v3">
            <div className="completion-content">
              <div className="completion-icon"></div>
              <h2>  !</h2>
              <p className="completion-text">
                  {generatedItems.length} {generatedItems.length === 1 ? '' : generatedItems.length < 5 ? '' : ''}.
                <br />
                    !
              </p>
              
              <div className="completion-stats">
                <div className="stat-item">
                  <span className="stat-icon"></span>
                  <span className="stat-label"></span>
                  <span className="stat-value">{generatedItems.length}</span>
                </div>
                <div className="stat-item">
                  <span className="stat-icon"></span>
                  <span className="stat-label"></span>
                  <span className="stat-value">{project?.name}</span>
                </div>
                <div className="stat-item">
                  <span className="stat-icon"></span>
                  <span className="stat-label"></span>
                  <span className="stat-value">{session?.name}</span>
                </div>
              </div>

              <div className="completion-actions">
                <button 
                  className="btn-primary-v3 completion-btn"
                  onClick={handleGenerateMore}
                >
                  <span className="btn-icon"></span>
                  <span> </span>
                </button>
                <button 
                  className="btn-secondary-v3 completion-btn"
                  onClick={handleViewGallery}
                >
                  <span className="btn-icon"></span>
                  <span> </span>
                </button>
              </div>

              <p className="completion-hint">
                       !
              </p>
            </div>
          </Card>
        )}

        {/* Loading Next Item */}
        {!generating && !showCompletionScreen && generatedItems.length > 0 && !currentItem && loadingNext && (
          <Card className="loading-next-card-v3">
            <div className="loading-next-content">
              <div className="loading-spinner-large"></div>
              <h2>   ...</h2>
              <p className="loading-next-text">
                : {progress.current}  {progress.total}
              </p>
              <div className="progress-bar">
                <div
                  className="progress-fill"
                  style={{ width: `${(progress.current / progress.total) * 100}%` }}
                ></div>
              </div>
              <p className="loading-hint">
                  ' ,    
              </p>
            </div>
          </Card>
        )}

        {/* Swipe Section */}
        {!generating && !showCompletionScreen && generatedItems.length > 0 && currentItem && (
          <div className="swipe-section-v3">
            <div className="swipe-header-v3">
              <h2>
                   {currentIndex + 1}  {progress.total}
              </h2>
              <div className="swipe-progress-badges">
                <span className="badge-current">: {currentIndex + 1}</span>
                <span className="badge-remaining">: {currentIndex}</span>
                {loadingNext && (
                  <span className="badge-generating">
                     : {progress.current}/{progress.total}
                  </span>
                )}
              </div>
            </div>

            <div className="swipe-layout-v3">
              {/* Left: Original Prompt */}
              <Card className="prompt-display-card">
                <h3>  Prompt</h3>
                <p>{currentItem.original_prompt || prompt}</p>
                <div className="prompt-meta">
                  <span className="meta-badge">Original</span>
                </div>
              </Card>

              {/* Center: Image & Rating Buttons */}
              <div className="rating-card-container-v3">
                <Card 
                  className="image-card-v3"
                  style={{
                    transform: `translate(${dragOffset.x}px, ${dragOffset.y}px) rotate(${dragOffset.x * 0.05}deg)`,
                    transition: isDragging ? 'none' : 'transform 0.3s ease',
                    cursor: isDragging ? 'grabbing' : 'grab'
                  }}
                  onTouchStart={handleTouchStart}
                  onTouchMove={handleTouchMove}
                  onTouchEnd={handleTouchEnd}
                  onMouseDown={handleMouseDown}
                  onMouseMove={handleMouseMove}
                  onMouseUp={handleMouseUp}
                  onMouseLeave={() => isDragging && handleMouseUp()}
                >
                  <img 
                    src={currentItem.url} 
                    alt="Generated content" 
                    className="generated-image-v3"
                    draggable={false}
                  />
                  
                  {/* Swipe indicators */}
                  {isDragging && (
                    <>
                      {Math.abs(dragOffset.x) > Math.abs(dragOffset.y) && Math.abs(dragOffset.x) > 30 && (
                        <div className={`swipe-indicator ${dragOffset.x > 0 ? 'right' : 'left'}`}>
                          {dragOffset.x > 0 ? ' +1' : ' -1'}
                        </div>
                      )}
                      {Math.abs(dragOffset.y) > Math.abs(dragOffset.x) && Math.abs(dragOffset.y) > 30 && (
                        <div className={`swipe-indicator ${dragOffset.y > 0 ? 'down' : 'up'}`}>
                          {dragOffset.y > 0 ? ' ' : ' +3'}
                        </div>
                      )}
                    </>
                  )}
                </Card>
                
                {/* 4 Rating Buttons (method.txt style) */}
                <div className="rating-buttons-v3">
                  <button
                    className="rating-btn super-dislike"
                    onClick={() => handleSwipe('super-down')}
                    disabled={currentIndex === generatedItems.length - 1 && (loadingNext || generating)}
                    title=" : -15   "
                  >
                    <span className="rating-icon"></span>
                    <span className="rating-label"></span>
                    <span className="rating-value">-3</span>
                  </button>
                  
                  <button
                    className="rating-btn dislike"
                    onClick={() => handleSwipe('left')}
                    disabled={currentIndex === generatedItems.length - 1 && (loadingNext || generating)}
                    title=": -5   "
                  >
                    <span className="rating-icon"></span>
                    <span className="rating-label"> </span>
                    <span className="rating-value">-1</span>
                  </button>
                  
                  <button
                    className="rating-btn like"
                    onClick={() => handleSwipe('right')}
                    disabled={currentIndex === generatedItems.length - 1 && (loadingNext || generating)}
                    title=": +5   "
                  >
                    <span className="rating-icon"></span>
                    <span className="rating-label"></span>
                    <span className="rating-value">+1</span>
                  </button>
                  
                  <button
                    className="rating-btn super-like"
                    onClick={() => handleSwipe('up')}
                    disabled={currentIndex === generatedItems.length - 1 && (loadingNext || generating)}
                    title=" : +15   "
                  >
                    <span className="rating-icon"></span>
                    <span className="rating-label">!</span>
                    <span className="rating-value">+3</span>
                  </button>
                </div>
                
                {/*  warning      */}
                {currentIndex === generatedItems.length - 1 && (loadingNext || generating) && (
                  <div className="next-photo-loading-warning">
                    <span className="loading-spinner-small"></span>
                    <span>    ({progress.current}/{progress.total})...</span>
                  </div>
                )}
                
                <button
                  className="skip-btn-v3"
                  onClick={() => handleSwipe('down')}
                  disabled={currentIndex === generatedItems.length - 1 && (loadingNext || generating)}
                >
                    ( )
                </button>
              </div>

              {/* Right: Enhanced Prompt */}
              <Card className="prompt-display-card enhanced">
                <h3> AI Enhanced</h3>
                <p className="scrollable-text">{currentItem.enhanced_prompt || currentItem.final_prompt}</p>
                <div className="prompt-meta">
                  <span className="meta-badge enhanced">Enhanced</span>
                  <span className="meta-info">{currentItem.model || 'GPT-4o + Seedream 4'}</span>
                </div>
              </Card>
            </div>
          </div>
        )}

        {/* Comment Modal */}
        {showCommentModal && (
          <div className="modal-overlay" onClick={() => setShowCommentModal(false)}>
            <div className="modal-content" onClick={(e) => e.stopPropagation()}>
              <div className="modal-header">
                <h3>   (')</h3>
                <button className="modal-close" onClick={() => setShowCommentModal(false)}>
                  
                </button>
              </div>

              <div className="modal-body">
                <p>
                  {pendingRating?.direction === 'up' && '   ?'}
                  {pendingRating?.direction === 'right' && '    ?'}
                  {pendingRating?.direction === 'left' && '   ?'}
                </p>
                <textarea
                  value={comment}
                  onChange={(e) => setComment(e.target.value)}
                  placeholder="  ..."
                  rows={4}
                  className="modal-textarea"
                  autoFocus
                />
              </div>

              <div className="modal-footer">
                <Button variant="secondary" onClick={skipRating}>
                  
                </Button>
                <Button onClick={submitRating}>
                   
                </Button>
              </div>
            </div>
          </div>
        )}
      </div>
    </div>
  );
}

export default GeneratePageV3;
